
.card {
  min-width: 210px;
  min-height: 110px;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px;
  margin: 20px;
  max-width: 250px;
  border-radius: 15px;
  border:2px black solid;
  box-shadow: 0px 0px 2px 2px rgb(0,0,0);
  .buttons{
    display:flex;
    justify-content:space-between;
    width:100%;
    & > * {
      width: 100%;
    }
    .next {
      background: lightgreen;
      &.notSolved {
        filter: blur(2px);
      }
    }
    .retreat {
      background: lightcoral;
    }
    input {
      border: none;
    }
  }
  .description {
    display: flex;
    span {
      text-align: center;
    }
  }
}
